import search from '../headsearch/headsearch.vue'
const tools = require('../../common/tools.js');
const server = require('../../common/server.js');
import mpvueCityPicker from '../../components/mpvue-citypicker/mpvueCityPicker.vue'
// import cityData from '../../common/new-city.data.js';

import empty from '../empty/empty.vue';
export default {
	components:{
		empty,
		mpvueCityPicker,
		search
	},
	data() {
		return {
			themeColor: '#007AFF',
			classBool:false,
			index:0,
			sectionList:[],
			departmentId:"",
			sectionNameList:[],
			doctorList:[],
			server:server.domain,
			cityPickerValueDefault: [0, 0, 1],
			areaId:"",
			areaText:"地区",
			areaBool:false,
			hospitalList:[],
			loginBool:false,
			search:""
		}
	},
	onLoad() {
		this.getSectionList();
		if(uni.getStorageSync("sessionId") != "" && uni.getStorageSync("sessionId") != null){
			this.loginBool = true;
		}
	},
	methods: {
		collect(id,ifCollection,index){
			if(!this.loginBool){
				uni.showModal({
					content:"需要登录才能收藏该推文，\n是否去登录",
					confirmText:"去登录",
					confirmColor:"#DD3535",
					cancelColor:"#999",
					success:function(res){
						if (res.confirm) {
							uni.navigateTo({
								url:"/pages/login/login"
							})
						} 
					}
				})
				return;
			}
			let that = this;
			let params = { 
				typeId:id,
				type:1,
				status:ifCollection==1 ? 2 : 1
			};
			tools.request("/api/index/isCollection.json", params,1,true).then(function(data) {
				if(ifCollection==1){
					tools.toast("已取消收藏");
				}else{
					tools.toast("已收藏");
				}
				that.doctorList[index].ifCollection = that.doctorList[index].ifCollection==1 ? 2 : 1;
			})
			
		},
		input(){
			this.doctorList = [];
			this.hospitalList = [];
			this.getList();
		},
		onCancel(e) {
			console.log(e);
			this.areaBool = !this.areaBool
		},
		// 三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
			this.areaBool = !this.areaBool
		},
		onConfirm(e) {
			console.log(e)
			this.areaId = e.cityCode;
			this.areaText = e.label;
			this.areaBool = !this.areaBool
			this.doctorList = [];
			this.hospitalList = [];
			this.getList();
		},
		detail(id,type){
			if(type == 1){
				tools.jumpTo("/pages/doctor/doctor?id="+id)
			}else if(type == 2){
				tools.jumpTo("/pages/outpatient/outpatient?id="+id)
			}
		},
		getList(){
			let that = this;
			let params = {
				pageSize: 10,
				pageNumber: 1,
				searchType: 1,
				departmentId:this.departmentId,
				areaId: this.areaId,
				search:this.search
			};
			if(tools.isLogin()){
				params.sessionId = uni.getStorageSync("sessionId")
			}
			tools.request("/api/index/searchDoctorOrHospital.json", params).then(function(data) {
				for(let i in data.doctorList){
					data.doctorList[i].searchType = 1;
				}
				that.doctorList = data.doctorList;
			})
			
			params.searchType = 2;
			tools.request("/api/index/searchDoctorOrHospital.json", params).then(function(data) {
				that.hospitalList = that.hospitalList.concat(data.hospitalList)
			})
		},
		getSectionList(){
			let that = this;
			tools.request("/api/index/departmentList.json", {}).then(function(data) {
				that.sectionList = data.departmentDTOList;
				
				that.sectionList.map( item => {
					that.sectionNameList.push(item.name);
				})
				
				if(that.sectionList.length > 0){
					that.departmentId = that.sectionList[0].id
				}
				that.getList();
			})
		},
		bindSectionChange(event){
			this.index = event.detail.value
			this.departmentId = this.sectionList[this.index].id;
			console.log(this.departmentId)
			this.doctorList = [];
			this.hospitalList = [];
			this.getList();
			this.classBool = !this.classBool;
		},
	}
}
